
















































































































































































































































.planTopic {
  margin: 30px auto;
}

.planTitle__text {
  width: 200px;
}

.planJob {
  margin: 30px auto;
  img {
    vertical-align: bottom;
    margin-left: 20px;
    margin-bottom: 4px;
  }
  .addBtn {
    display: inline-block;
    float: right;
  }
}

label {
  padding: 0 !important;
  font-size: 1rem !important;
  font-weight: normal !important;
  &:before {
    display: none;
  }
  &:after {
    display: none;
  }
}

.teamClass__block input[type="checkbox"] {
  position: relative !important;
  left: auto;
  -webkit-appearance: checkbox;
}

select {
  background-color: #fff;
  border: 1px solid #ccc;
  padding: 12px;
  border-radius: 4px;
}

.flexRow {
  display: flex;
  align-items: center;
  justify-content: space-around;
}
