  background: #66cdb6;
  color: #fff;
  border-radius: 10px;
  padding: 10px;
}

.active {
  color: #66cdb6;
}

.activeCircle {
    border-radius: 100%;
    width: 20px;
    height: 20px;
    border: 4px solid #EAEAEA;
    background: #66CDB6;
    display: inline-block;
    vertical-align: middle;
}
