  padding: 0 !important;
  font-size: 1rem !important;
  font-weight: normal !important;
  &:before {
    display: none;
  }
  &:after {
    display: none;
  }
}

.teamClass__block input[type="checkbox"] {
  margin: 0 12px 0 4px;
  position: relative !important;
  left: auto;
  -webkit-appearance: checkbox;
}

.planJob {
  margin: 15px 0;
  .planTitle__text {
    width: 200px;
  }
}

.addBtn {
      display: inline-block;
    float: right;
}
