













































































































.planTitle {
  width: 180px;
}

.styleList {
  width: 100%;
  display: grid;
  grid-template-columns: 3fr 1fr;
  &__left {
    display: flex;
    justify-content: space-between;
    border-right: 3px solid #d4d4d4;
    padding-right: 8px;
    margin-right: 8px;
    .styleBlock {
      display: flex;
      align-items: center;
          padding: 12px 20px;
    flex-direction: column;
    background: #fff;
    img {
      margin: 6px auto;
    }
      &__btn {
        border: 1px solid #d4d4d4;
        border-radius: 4px;
        padding: 2px 10px;
      }
    }
  }
}

input[type="radio"] {
  display: inline-block;
  left: unset;
  position: relative;
  width: 30px;
  height: 30px;
  border: 3px solid #d4d4d4;
  border-radius: 50em;
  -webkit-appearance: auto;
}

.deleteBtn {
  width: 50px;
  height: 28px;
  line-height: 28px;
  text-align: center;
  background:#f45454;
  color:#fff;
  border-radius: 4px;
}
